import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote
} = app;

export default {
	data() {
		return {
			kind: 0,
			activeTab: 2,
			loading: true,
			firstId:'',
			secondId:'',
			page:1,
			num:10,
			firstCate:[],
			secondCate:[],
			list:[],
			config:[]
		};
	},
	methods: {
		mpItem() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp
			};
			let {
				firstId,
				secondId,
				page,
				num
			} = this;

			if (page != 1) {
				this.list = uni.getStorageSync('classify')
				return;
			}
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign,
					firstId: firstId,
					secondId: secondId,
					page: 1,
					num: num
				},
				obj
			);
			rs.getRequest('mpItemList', params, res => {
				let data = res.data;
				if (data.code == 200) {
					// if (!firstId) {
					// 	firstId = data.data.firstCate[0].id;
					// }
					// if (firstId) {
					// 	data.data.firstCate.map((e, index) => {
					// 		if (firstId == e.id) {
					// 			this.active = index;
					// 		}
					// 	});
					// }
					this.config = data.data;
					this.firstCate = data.data.firstCate;

					this.secondCate = data.data.secondCate;
					if (data.data.list.length == 0) {
						this.bitmap = true;
					} else {
						this.list = data.data.list;
						this.bitmap = false;
						if (data.data.list.length < 10) {
							this.loading = false;
						} else {
							this.loading = true;
						}
					}
				}
			});
		},
		// 切换一级分类
		changeFirst(index) {
			this.firstId=this.firstCate[index].id;
			this.secondId="";
			this.kind = 0;
			this.mpItem();
		},
		// 切换二级分类
		changeSecond(index) {
			this.secondId=this.secondCate[index].id;
			this.kind = index;
			this.mpItem();
			
		},
		// 显示键盘
		showKey() {
			this.$refs.popup.open();
			uni.hideTabBar()
		}
	},
	onShow() {
		console.log(45)
		this.mpItem();
	}
};
